.hotline {
  width: 860px;
  height: 269px;
}
.hotline .title {
  width: 137px;
  height: 37px;
  line-height: 37px;
  text-align: center;
  font-size: 16px;
  letter-spacing: 1px;
  color: #ffffff;
}
.hotline .kuang {
  margin-top: 10px;
  width: 860px;
  height: 222px;
  overflow: hidden;
}
.hotline .kuang .bj {
  margin: 14px 0 0 13px;
  width: 812px;
  height: 200px;
  background-image: linear-gradient(90deg, rgba(2, 208, 255, 0.1) 0%, rgba(22, 32, 99, 0.1) 100%);
  overflow: hidden;
}
.hotline .kuang .bj .left {
  float: left;
  display: inline-block;
  width: 184px;
  height: 572px;
}
.hotline .kuang .bj .left ul {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}
.hotline .kuang .bj .left ul li {
  margin: 13px 0 0 9px;
  list-style: none;
  width: 184px;
  height: 52px;
  overflow: hidden;
}
.hotline .kuang .bj .left ul li span {
  display: inline-block;
  font-size: 13px;
  letter-spacing: 1px;
  color: #ffffff;
}
.hotline .kuang .bj .left ul li span i {
  font-size: 26px;
  font-weight: 700;
  font-style: normal;
  color: #00ffff;
}
.hotline .kuang .bj .month {
  float: left;
  width: 284px;
  height: 163px;
}
.hotline .kuang .bj .month .box {
  width: 100%;
  height: 100%;
  margin-left: 22px;
}
.hotline .kuang .satisfied {
  float: left;
  margin-left: 55px;
  margin-top: 8px;
  width: 286px;
  height: 195px;
  overflow: hidden;
}
.hotline .kuang .satisfied .top_box {
  width: 286px;
  height: 20px;
}
.hotline .kuang .satisfied .top_box p {
  width: 20%;
  height: 20px;
  float: left;
  font-size: 14px;
  letter-spacing: 1px;
  color: #ffffff;
}
.hotline .kuang .satisfied .top_box ul {
  float: left;
  margin: 0;
  padding: 0;
  width: 60%;
  height: 20px;
  margin-top: 3px;
}
.hotline .kuang .satisfied .top_box ul li {
  float: left;
  margin-left: 7px;
  display: inline-block;
  width: 25px;
  height: 16px;
}
.hotline .kuang .satisfied .top_box span {
  float: left;
  font-style: 14px;
  letter-spacing: 1px;
  color: #f8a036;
  margin-top: 3px;
}
.hotline .kuang .satisfied .rx {
  width: 274px;
  height: 169px;
}
.hotline .kuang .satisfied .rx .box {
  width: 100%;
  height: 100%;
}
.call1 {
  margin-left: 65px;
}
.call2 {
  margin-left: 51px;
}
.call3 {
  margin-left: 28px;
}
